      <!--    Introduction    -->
      <el-row style="text-align: center">
        <h1 style="font-size: 100px; margin: 0;"><span :style="{'color': primaryColor}">KE</span>LLECT</h1>
        <h2 style="font-size: 40px; margin: 0;">a Kernel-based Efficient and Lossless Event Log Collector</h2>
      </el-row>

      <el-row style="text-align: center; align: center; margin-top: 3%">
        <img src="./assets/img/framework.png" style="width:55%; margin-bottom: 1%">
      </el-row>

      <el-row>
        <el-col :offset="4" :span="16">
          <p><span class="justify">
            Kellect is a Kernel-based efficient and Lossless event log collector, a systematic framework for the full
            processing of kernel-level logs, including collection, cleanup, confusion, storage, and analysis.</span></p>

          <p><span class="justify">As a firstborn version, kellect is a multi-threaded Windows kernel log collector based on ETW (Event Tracing
            for Windows), developed in C++ language with high-efficient performance. kellect can track kernel-level information
            of Windows systems, such as FileIO, Process, Thread, ImageLoad, Registry, and so on.</span></p>

          <p><span class="justify">The program integrates the full functions of event collection, event analysis, event semantic correction, and
            event output. Output in JSON format and can specify the output to a file path or transfer to another host through
            the kafka.</span></p>

          <p><span class="justify">We implemented most of the functionality, which means users can use this tool via start PowerShell.exe or cmd.exe
            and specify the command-line options and parameters as needed without modifying the source code. We also provide
            configuration files that users can customize to suit their needs. and the comparison with other tools as below:</span></p>

        </el-col>
      </el-row>

